	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/3/andy-rubin-steps-down-as-head-of-android/index.md absPath of file >>> MarkdownRemark","html":"<p>I’m not entirely sure what to make of this, but Rubin’s replacement Sundar Pichai has proven himself with the Chrome browser and it will be interesting to see how his approach wil differ from Rubin’s.</p>\n<blockquote>\n<p>But his insight immediately struck a chord because at the time it was extremely painful developing services for mobile devices. We had a closet full of more than 100 phones and were building our software pretty much device by device. It was nearly impossible for us to make truly great mobile experiences.</p>\n</blockquote>\n<p>That’s pretty ironic given how difficult it is to develop an app that suits every Android device in the wild.</p>","frontmatter":{"title":"Andy Rubin steps down as Head of Android","date":"March 13, 2013","passthroughUrl":"http://googleblog.blogspot.co.uk/2013/03/update-from-ceo.html"}}},"pathContext":{"slug":"/blog/2013/3/andy-rubin-steps-down-as-head-of-android/","previous":{"fields":{"slug":"/blog/2013/3/living-with-a-lot-less/"},"frontmatter":{"title":"Living with a lot less","passthroughUrl":"http://www.nytimes.com/2013/03/10/opinion/sunday/living-with-less-a-lot-less.html?_r=1&pagewanted=all&"}},"next":{"fields":{"slug":"/blog/2013/3/google-removes-adblock-from-play-store/"},"frontmatter":{"title":"Google removes Adblock from Play store","passthroughUrl":"http://adblockplus.org/blog/adblock-plus-for-android-removed-from-google-play-store"}}}}

